import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
import { Link, Redirect } from 'react-router-dom'
// import MyBottomNavigation from './components/MyBottomNavigation'
import Container from './components/Container'
import { BrowserRouter } from 'react-router-dom';
import { BottomNavigation, BottomNavigationItem } from 'material-ui/BottomNavigation'
import FontIcon from 'material-ui/FontIcon';
import Paper from 'material-ui/Paper';
import IconLocationOn from 'material-ui/svg-icons/communication/location-on';
import Home from 'material-ui/svg-icons/action/home'
import Person from 'material-ui/svg-icons/social/person'
import ContentPaste from 'material-ui/svg-icons/content/content-paste'
import Work from 'material-ui/svg-icons/action/work'
import Contacts from 'material-ui/svg-icons/communication/contacts'

const homeIcon = <Home />
const aboutIcon = <Person />
const resumeIcon = <ContentPaste />
const portfolioIcon = <Work />
const contactIcon = <Contacts />
const recentsIcon = <Person />;
const favoritesIcon = <Contacts />
const nearbyIcon = <IconLocationOn />;

class App extends Component {
  state = {
      selectedIndex: 0,
  }
  items = [
    { label: "Home", icon: homeIcon, link: '/home'},
    { label: "About", icon: aboutIcon, link: '/about' },
    { label: "Resume", icon: resumeIcon, link: '/resume' },
    { label: "Portfolio", icon: portfolioIcon, link: '/portfolio' },
    { label: "Contact", icon: contactIcon, link: '/contact' }
  ]
  select = (index) => {
    this.setState({selectedIndex: index})
  }
    render(){
    const bottomNavigationStyle = {
      position: 'fixed',
      bottom: 0,
      left: 0,
      width: '100%',
    }
    return (
      <BrowserRouter>
        <div className="App">
          <Redirect to={this.items[this.state.selectedIndex].link}/>
          {/* <Redirect to={this.items[2].link}/> */}
          <Container />
          <Paper zDepth={1} style={bottomNavigationStyle} rounded={false}>
                <BottomNavigation selectedIndex={this.state.selectedIndex}>
                    { this.items.map((item, index) => (
                            <BottomNavigationItem
                                label={item.label}
                                icon={item.icon}
                                onClick={() => this.select(index)}>
                          </BottomNavigationItem>
                    ))}
                </BottomNavigation>
            </Paper>
        </div>
      </BrowserRouter>
    );
  }
}

export default App;



