import React, { Component } from 'react'
import Paper from 'material-ui/Paper'
import { Grid, Row ,Col} from 'react-flexbox-grid'
import RaisedButton from 'material-ui/RaisedButton'
import FlatButton from 'material-ui/FlatButton'
import {Link} from 'react-router-dom'

class Home extends Component {
    render(){
        const titleStyles = {
            fontWeight: 'bold',
            lineHeight: '1.4em',
            margin: 0,
            textTransform: 'uppercase',
            letterSpacing: '2px'
        }
        const subtitleStyles = {
            fontSize: '13px',
            color: '#00bcd5',
            fontWeight: 300
        }
        const descriptionStyles = {
            color: "rgba(0, 0, 0, .45)",
            margin: 0,
            fontWeight: 500,
            marginTop: '32px',
            lineHeight: '1.7em'
        }
        return(
            <Row middle="lg" style={{width: "100%", margin: 0}}>
                <Col xs={12} style={{padding: 0}}>
                    <Row center="lg" style={{margin: 0}}>
                        <Col xs={12} lg={10} style={{padding: 0}}>
                            <Paper>
                                <Row style={{margin: 0}}>
                                    <Col xs={12} lg={6} style={{padding: 0}}>
                                        <img src="https://scontent-sit4-1.cdninstagram.com/t51.2885-15/e35/13249889_287332208272845_386579222_n.jpg" style={{width: '100%', height: '100%'}}/>
                                    </Col>
                                    <Col xs={12} lg={6} style={{textAlign:'left', padding: 16}}>
                                        <h1 style={titleStyles}>I’m Alfian Akmal Hanantio</h1>
                                        <span style={subtitleStyles}>Android Developer</span>
                                        <p style={descriptionStyles}>
                                        My Name is Alfian Akmal Hanantio, I'm an Android Developer based in Indonesia and Kotlin Antusias. 
                                        I started developing an Android Apps about 2 years ago.
                                        I Love to Explore a new Technology.
                                        </p>
                                        <Row center="xs" style={{marginTop: 32 }}>
                                            <Col xs={12} lg={8}>
                                            <Link to="/contact">
                                            <RaisedButton label="Contact Me" primary={true} style={{width: '100%'}}/>
                                            </Link>
                                            </Col>
                                            <Col xs={12} lg={8}>
                                            <FlatButton label="Download Resume" primary={true} style={{width: '100%', marginTop: 8}}/>
                                            </Col>
                                        </Row>
                                    </Col>
                                </Row>
                            </Paper>
                        </Col>
                    </Row>
                </Col>
            </Row>

        )
    }
}

export default Home


