import React from 'react'
import Paper from 'material-ui/Paper'
import { Grid, Row ,Col} from 'react-flexbox-grid'
import RaisedButton from 'material-ui/RaisedButton'
import FlatButton from 'material-ui/FlatButton'
import { List } from 'material-ui/List';
import { ListItem, Divider, LinearProgress } from 'material-ui';

class Resume extends React.Component {
    educations = [
        { grade: 'Senior High School', at: 'SMAN 1 Waru, Sidoarjo', start: 2013, end: 2016 },
        { grade: 'Junior High School', at: 'SMPN 4 Waru, Sidoarjo', start: 2011, end: 2013 },
        { grade: 'Junior High School', at: 'SMPN 2 Sedati, Sidoarjo', start: 2010, end: 2013 },
        { grade: 'Primary School', at: 'SDN Pabean 1, Sidoarjo', start: 2004, end: 2010 },
    ]
    jobs = [
        {start: 'Feb, 2016', end: 'Present', at: 'CV. Sarana Komunikasi Indonesia', position: 'Android Developer'},

    ]

    render(){
        const titleStyles = {
            fontWeight: 'bold',
            lineHeight: '1.4em',
            margin: 0,
            fontSize: 24,
            textTransform: 'uppercase',
            letterSpacing: '2px'
        }
        const subtitleStyles = {
            fontSize: '13px',
            color: '#00bcd5',
            fontWeight: 500
        }
        const descriptionStyles = {
            fontSize: '13px',
            color: "rgba(0, 0, 0, .45)",
            margin: 0,
            fontWeight: 500,
            marginTop: '32px',
            lineHeight: '1.7em'
        }
        return(
            <Row style={{width: "100%", margin: 0}}>
                <Col xs={12} style={{padding: 0}}>
                    <Row center="lg" style={{margin: 0}}>
                        <Col xs={12} lg={10} style={{padding: 0}}>
                            <Paper rounded={false} style={{minHeight: 'calc(100vh - 56px)'}}>
                                <Row style={{margin: 0}}>
                                    <Col xs={12} style={{textAlign:'left', padding: 16}}>
                                        <h1 style={titleStyles}>My Education</h1>
                                        <span style={subtitleStyles}>Take a look at the places where I've studied</span>
                                        <Row>
                                            <Col xs={12}>
                                                <List>
                                                    { this.educations.map((edu, index) => (
                                                        <div>
                                                        <ListItem
                                                            primaryText={edu.grade}
                                                            secondaryText={
                                                                <p>
                                                                    <strong>{edu.grade} - </strong>
                                                                    {edu.start} to {edu.end}
                                                                </p>
                                                            }
                                                            disabled
                                                        />
                                                        { index == this.educations.length -1 ? '' : <Divider />}
                                                        </div>
                                                    ))}
                                                </List>
                                            </Col>
                                        </Row>
                                        <Divider style={{marginTop: 16, marginBottom: 8}}/>
                                    </Col>
                                    <Col xs={12} style={{textAlign:'left', padding: 16}}>
                                        <h1 style={titleStyles}>My Experience</h1>
                                        <span style={subtitleStyles}>Take a look at the places where I've Worked</span>
                                        <Row>
                                            <Col xs={12}>
                                                <List>
                                                    { this.jobs.map((job, index) => (
                                                        <div>
                                                        <ListItem
                                                            primaryText={job.at}
                                                            secondaryText={
                                                                <p>
                                                                    {job.start} - {job.end}
                                                                    <strong> as {job.position}</strong>
                                                                </p>
                                                            }
                                                            disabled
                                                        />
                                                        { index == this.jobs.length -1 ? '' : <Divider />}
                                                        </div>
                                                    ))}
                                                </List>
                                            </Col>
                                        </Row>
                                    </Col>
                                    {/* <Col xs={12} style={{textAlign:'left', padding: 16}}>
                                        <h1 style={titleStyles}>My Skills</h1>
                                        <span style={subtitleStyles}>I've got really amazing skills</span>
                                        <Row style={{marginTop: 16}}>
                                            { this.skills.map((skill, index) => (
                                                <Col xs={12} lg={6}>
                                                    <h3>{skill.name}</h3>
                                                    <LinearProgress mode='determinate' value={skill.value}/>
                                                </Col>
                                            ))}
                                        </Row>
                                    </Col> */}
                                </Row>
                            </Paper>
                        </Col>
                    </Row>
                </Col>
            </Row>
        )
    }
}

export default Resume


